import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./pages";
import SigningPage from "./pages/signin";
import ProjectMIDAPage from "./pages/projectMIDA";
import ProjectARVRPage from "./pages/projectARVR";
import ProjectRichenPage from "./pages/projectRichen";
import ProjectMIASPage from "./pages/projectMIAS";
import ProjectEGCDPage from "./pages/projectEGCD";
function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" component={Home} exact />
        <Route path="/signin" component={SigningPage} exact />
        <Route path="/project_MIDA" component={ProjectMIDAPage} exact />
        <Route path="/project_ARVR" component={ProjectARVRPage} exact />
        <Route path="/project_Richen" component={ProjectRichenPage} exact />
        <Route path="/project_MIAS" component={ProjectMIASPage} exact />
        <Route path="/project_EGCD" component={ProjectEGCDPage} exact />
      </Switch>
    </Router>
  );
}

export default App;
