import styled from "styled-components";
import { MdKeyboardArrowRight, MdArrowForward } from "react-icons/md";
import { keyframes } from "styled-components";

const flash = keyframes`
0%  {
  opacity: 1;
}
20% {
  opacity: 1;
}
25% {
  opacity: 0;
}
30% {
  opacity: 1;
}
70% {
  opacity: 1;
}
90% {
  opacity: 1;
}
95% {
  opacity: 0;
}
100% {
  opacity: 1;
}
`;
const dash = keyframes`
from {
  stroke-dashoffset: 1000;
  stroke-width: 3px;
}
to {
  stroke-dashoffset: 0;
  stroke-width: 3px;
}
`;

export const HeroContainer = styled.div`
  background: #0c0c0c;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  height: 100vh;
  position: relative;
  z-index: 1;

  :before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.2) 0%,
        rgba(0, 0, 0, 0.6) 100%
      ),
      linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, transparent 100%);
    z-index: 2;
  }
`;

export const HeroBg = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;

  /*  Add :before styles  */
`;

